
import router from '@/router';
import { inject } from 'vue'
export default {
  name: "Header",
  setup() {
    const pageinfo: string = inject('pageinfo') || '';
    let Ifshow1 = true;
    let Ifshow2 = false;
    if (pageinfo == 'HomePage') {
      Ifshow1 = false;
      Ifshow2 = true;
    }
    const logout = () => {
      localStorage.removeItem("token");
      router.push("/");
    }
    return {
      Ifshow1,
      Ifshow2,
      logout
    }
  },
};
